@font-face {
  font-family: 'Open Sans';
  font-style: normal;
  font-weight: 300;
  src: local('Open Sans Light'), local('OpenSans-Light'), url(https://fonts.gstatic.com/s/opensans/v17/mem5YaGs126MiZpBA-UN_r8OVuhs.ttf) format('truetype');
}
@font-face {
  font-family: 'Open Sans';
  font-style: normal;
  font-weight: 400;
  src: local('Open Sans Regular'), local('OpenSans-Regular'), url(https://fonts.gstatic.com/s/opensans/v17/mem8YaGs126MiZpBA-UFUZ0e.ttf) format('truetype');
}
@font-face {
  font-family: 'Open Sans';
  font-style: normal;
  font-weight: 600;
  src: local('Open Sans SemiBold'), local('OpenSans-SemiBold'), url(https://fonts.gstatic.com/s/opensans/v17/mem5YaGs126MiZpBA-UNirkOVuhs.ttf) format('truetype');
}
@font-face {
  font-family: 'Open Sans';
  font-style: normal;
  font-weight: 700;
  src: local('Open Sans Bold'), local('OpenSans-Bold'), url(https://fonts.gstatic.com/s/opensans/v17/mem5YaGs126MiZpBA-UN7rgOVuhs.ttf) format('truetype');
}
:active,
:hover,
:focus {
  outline: 0;
  outline-offset: 0;
}
.btn-group > .btn,
.btn-group-vertical > .btn {
  outline: none;
}
.img-thumbnail {
  border: 0;
}
.wrapper {
  overflow-x: hidden;
}
html {
  font-size: 10px;
}
body {
  font-size: 16px;
}
body header {
  background: linear-gradient(rgba(0, 0, 0, 0.3)), url('../../build/img/22-min.jpg') center center no-repeat;
  background-size: cover;
  padding-bottom: 7rem;
}
body header .header-contact-container {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-around;
  width: 100%;
  margin: 8rem 0;
  align-items: center;
}
body header .header-contact-container .connection {
  text-align: right;
  line-height: 0.5em;
}
body header .header-contact-container .connection .connection__tel a {
  text-decoration: none;
  color: #ffffff;
  font-size: 1.875em;
  line-height: 1.5em;
  font-weight: bold;
  font-family: 'Open Sans', sans-serif;
}
body header .header-contact-container .connection .connection__addr p {
  font-size: 1.2em;
  color: #ffffff;
  font-family: 'Open Sans', sans-serif;
  font-weight: 500;
}
body header .header-contact-container .connection .connection__btn button {
  border-radius: 30px;
  border: 3px solid #FFBB00;
  padding: 1rem 1.5rem;
  color: #ffffff;
  font-size: 1em;
  background: transparent;
  font-weight: 600;
  transition: all 0.2s;
  outline: none;
}
body header .header-contact-container .connection .connection__btn button:hover {
  background-color: #FFBB00;
  transform: scale(1.1);
  transition: all 0.2s;
}
body header .header-title {
  display: flex;
  justify-content: center;
}
body header .header-title h1 {
  color: #ffffff;
  font-size: 4em;
  font-family: 'Open Sans', sans-serif;
  font-weight: 700;
  text-align: center;
}
body header .header-title h1 span {
  color: #FFC300;
}
body header .header-description {
  display: flex;
  justify-content: center;
  margin: 8rem 0;
}
body header .header-description h2 {
  color: #ffffff;
  font-size: 1.8em;
  font-family: 'Open Sans', sans-serif;
  text-align: center;
}
body header .header-description h2 span {
  color: #FFC300;
}
body header .header-form {
  width: 100%;
  display: flex;
  justify-content: center;
}
body header .header-form input,
body header .header-form button {
  min-height: 3em;
  font-size: 1.125em;
  padding: 1rem 2rem;
  font-weight: 400;
  font-family: 'Open Sans', sans-serif;
  margin: 0 1rem;
  border-radius: 10px;
  border: none;
}
body header .header-form input::placeholder,
body header .header-form button::placeholder {
  color: #C4C4C4;
}
body header .header-form input:hover::placeholder,
body header .header-form button:hover::placeholder {
  color: #787878;
}
body header .header-form button {
  background-color: #ffc933;
  outline: none;
}
body header .header-form button:hover {
  background-color: #FFBB00;
}
body .tours {
  margin-bottom: 5rem;
}
body .tours .tours-title {
  font-family: #ffffff;
  text-align: center;
  margin: 5rem 0;
}
body .tours .tours-title h2 {
  font-size: 2.5em;
}
body .tours .tours-title p {
  font-size: 1em;
}
body .tours .tours-wrapp {
  position: relative;
  display: flex;
  align-items: flex-start;
}
body .tours .tours-wrapp .tours--wrapp-items {
  max-width: 77.02%;
}
body .tours .tours-wrapp .tours--wrapp-items .tours-items {
  display: flex;
}
body .tours .tours-wrapp .tours-items--item {
  position: relative;
}
body .tours .tours-wrapp .tours-items--item .tours-items--item-info {
  position: absolute;
  background: linear-gradient(rgba(0, 0, 0, 0.5));
  width: 98.4%;
  top: 3px;
  left: 3px;
  z-index: 1;
  padding: 1rem 0;
  color: #fff;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  color: #ffffff;
  font-family: "Open Sans";
}
body .tours .tours-wrapp .tours-items--item .tours-items--item-info h4 {
  font-size: 1.5em;
}
body .tours .tours-wrapp .tours-items--item .tours-items--item-info p {
  font-size: 0.8em;
}
body .tours .tours-wrapp--reverse {
  display: flex;
  flex-direction: row-reverse;
}
body .advantage {
  background: linear-gradient(rgba(0, 0, 0, 0.3)), url('../../build/img/our-advantages.jpg') center center no-repeat;
  background-size: cover;
  display: flex;
  justify-content: center;
}
body .advantage .advantage-title {
  display: flex;
  justify-content: center;
  width: 100%;
  margin: 3rem 0;
}
body .advantage .advantage-title h2 {
  font-size: 2.5em;
  font-family: 'Open Sans';
  color: #ffffff;
  font-weight: 600;
}
body .advantage .wrapp-adv {
  width: 100%;
  display: flex;
  justify-content: center;
}
body .advantage .wrapp-adv .advantage-items {
  text-align: center;
}
body .advantage .wrapp-adv .advantage-items .advantage-item {
  margin: 3rem 0;
  position: relative;
  display: flex;
  justify-content: center;
  align-items: center;
}
body .advantage .wrapp-adv .advantage-items .advantage-item:nth-child(1) {
  left: 1px;
}
body .advantage .wrapp-adv .advantage-items .advantage-item:nth-child(2) {
  left: 25px;
}
body .advantage .wrapp-adv .advantage-items .advantage-item:nth-child(3) {
  left: 50px;
}
body .advantage .wrapp-adv .advantage-items .advantage-item:nth-child(4) {
  left: 75px;
}
body .advantage .wrapp-adv .advantage-items .advantage-item:nth-child(5) {
  left: 100px;
}
body .advantage .wrapp-adv .advantage-items .advantage-item:nth-child(6) {
  left: 120px;
}
body .advantage .wrapp-adv .advantage-items .advantage-item img {
  width: 75px;
  border-radius: 50%;
}
body .advantage .wrapp-adv .advantage-items .advantage-item p {
  font-size: 1.2em;
  font-weight: 500;
  font-family: 'Open Sans';
  color: #ffffff;
  width: 50%;
  margin-left: 2rem;
  line-height: 1em;
  display: inline-block;
  text-align: left;
}
body .services {
  background: linear-gradient(rgba(0, 0, 0, 0)), url('../../build/img/bg-services1.jpg') center center no-repeat;
  background-size: cover;
  padding: 4rem 0;
}
body .services .services-title {
  text-align: center;
  margin: 3rem 0 5rem 0;
}
body .services .services-title h3 {
  font-size: 1.5em;
  font-weight: 600;
}
body .services .services-card {
  margin: 1rem 0;
}
body .services .card {
  height: 100%;
  display: flex;
  justify-content: center;
  flex-direction: column;
  align-items: center;
  padding: 2rem 0;
  font-family: "Open Sans";
  box-shadow: 1px 1px 5px #000;
  border-radius: 5px;
}
body .services .card .card-img-top {
  height: 50px;
  width: 50px;
  display: flex;
  justify-content: center;
}
body .services .card .card-body .card-title {
  font-size: 1em;
  text-align: center;
  font-weight: 600;
}
body .services .card .card-body .card-text {
  font-size: 0.8em;
  text-align: center;
}
body .services .services-btn {
  text-align: center;
  margin: 3rem 0;
}
body .services .services-btn button {
  min-height: 3em;
  font-size: 1.125em;
  padding: 1rem 2rem;
  font-weight: 400;
  font-family: 'Open Sans', sans-serif;
  margin: 0 1rem;
  border-radius: 10px;
  border: none;
  background-color: #FFBB00;
  outline: none;
}
body .services .services-btn button:hover {
  background-color: #ffc933;
}
body .faq .faq-wrapp {
  margin: 6rem;
}
body .faq .faq-wrapp #accordion .card .card-body {
  font-size: 0.9em;
}
body .faq .faq-wrapp #accordion .card .btn-link {
  color: #000000;
  font-size: 1.5em;
  text-decoration: none;
  font-weight: 600;
  text-align: left;
}
body .footer {
  background: linear-gradient(rgba(0, 0, 0, 0.1)), url('../../build/img/bg-footer.jpg') center center no-repeat;
  background-size: cover;
  padding: 3rem 0;
}
body .footer .footer-info {
  display: flex;
  flex-direction: column;
  justify-content: center;
}
body .footer .footer-info p {
  margin: 0;
  line-height: 1.2em;
  color: #ffffff;
}
body .footer .footer-social {
  display: flex;
  align-items: center;
  justify-content: center;
  margin-top: 0.5rem;
}
body .footer .footer-social a {
  margin: 0 1rem;
}
body .footer .footer-social a img {
  width: 60px;
  border-radius: 55px;
}
body .footer .footer-bank-card {
  display: flex;
  align-items: center;
  justify-content: center;
  margin-top: 0.8rem;
}
body .footer .footer-bank-card p {
  color: #ffffff;
  margin-right: 2rem;
}
body .footer .footer-bank-card img {
  height: 50px;
}

@media screen and (max-width: 1200px) {
  body {
    font-size: 14px;
  }
}
@media screen and (max-width: 992px) {
  body header .header-form input,
  body header .header-form button {
    margin: 0;
  }
  body .tours .tours-wrapp .tours--wrapp-items {
    max-width: 76.78%;
  }
  body .tours .tours-wrapp .tours-items--item .tours-items--item-info {
    width: 97.5%;
    padding: 0.5rem 0;
  }
  body .advantage .wrapp-adv .advantage-items .advantage-item {
    position: static;
  }
  body .services .card .card-body .card-title {
    font-size: 1.2em;
  }
  body .services .card .card-body .card-text {
    font-size: 1em;
  }
}
@media screen and (max-width: 768px) {
  body {
    font-size: 13px;
  }
  body header .header-title h1 {
    font-size: 3.5em;
  }
  body header .header-description {
    margin: 5rem 0;
  }
  body header .header-form form {
    display: flex;
    flex-direction: column;
  }
  body header .header-form form input,
  body header .header-form form button {
    margin: 0.5rem 0;
  }
  body header .header-contact-container {
    margin: 5rem 0;
  }
  body header .header-contact-container .logo {
    text-align: center;
    margin: 3rem 0;
  }
  body header .header-contact-container .connection {
    text-align: center;
  }
  body .tours .tours-wrapp .tours-items--item .tours-items--item-info {
    width: 97.5%;
    padding: 0.3rem 0;
    font-size: 0.8em;
  }
  body .footer .footer-info {
    text-align: center;
  }
}
@media screen and (max-width: 767px) {
  body .footer .footer-social {
    margin: 2rem 0 1rem 0;
  }
  body .footer .footer-social a img {
    width: 40px;
  }
}
@media screen and (max-width: 575px) {
  body {
    font-size: 12px;
  }
  body header .header-title h1 {
    font-size: 2.5em;
  }
  body .tours .tours-desktop {
    display: none;
  }
  body .tours .tours-wrapp .tours--wrapp-items {
    max-width: 100%;
  }
  body .tours .tours-wrapp .tours-items--item .tours-items--item-info {
    width: 99%;
    padding: 0.5rem 0;
  }
  body .tours .tours-wrapp .tours-items--item .tours-items--item-info h4 {
    font-size: 3em;
  }
  body .tours .tours-wrapp .tours-items--item .tours-items--item-info p {
    font-size: 1.2em;
  }
  body .advantage .advantage-title h2 {
    font-size: 2em;
  }
  body .faq .faq-wrapp {
    margin: 4rem 0;
  }
  body .faq .faq-wrapp #accordion .card .btn-link {
    font-size: 1.2em;
  }
}
